<template>
    <section class="dropbox">
        <FallingBlock v-for="(block, index) in fallingBlocks"
                      :block="block"
                      :index="index"
                      :key="block.id">
        </FallingBlock>
    </section>
</template>

<script>
    import FallingBlock from './FallingBlock'
    import {mapMutations} from 'vuex'
    import {INITIALIZE_FALLING_BLOCKS, MOVE_LEFT, MOVE_RIGHT} from '../constants'

    export default {
        name: "Dropbox",
        components: {
            FallingBlock
        },
        computed: {
            fallingBlocks() {
                return this.$store.state.fallingBlocks;
            }
        },
        methods: {
            ...mapMutations({
                initializeFallingBlocks: INITIALIZE_FALLING_BLOCKS,
                moveBlockRight: MOVE_RIGHT,
                moveBlockLeft: MOVE_LEFT,
            }),
            onKeyDown(ev) {
                if (ev.keyCode === 39) this.moveBlockRight();
                if (ev.keyCode === 37) this.moveBlockLeft();
            },
        },
        beforeMount() {
            this.initializeFallingBlocks();
            window.addEventListener('keydown', this.onKeyDown)
        },
        beforeDestroy() {
            window.removeEventListener('keydown', this.onKeyDown)
        }
    }
</script>

<style lang="scss" scoped>
    .dropbox {
        height: 65%;
        width: 100%;
        position: relative;
    }
</style>